import React from 'react';
import ReactDOM from 'react-dom';
import './Navigation.css';
let showMenu = false;

class Navigation extends React.Component{
  toggleMenu(event) {
    // Select DOM items (UI Structure)
    const menuBtn = document.querySelector('.menu-btn');
    const menu = document.querySelector('.menu');
    const menuNav = document.querySelector('.menu-nav');
    const menuBranding = document.querySelector('.menu-branding');
    const navItems = document.querySelectorAll('.nav-item');

    // Set Inital state of menu
  if (!showMenu) {
      menuBtn.classList.add('close');
      menu.classList.add('show');
      menuNav.classList.add('show');
      navItems.forEach(item => item.classList.add('show'));
      // Set Menu State
      showMenu = true;
  } 
  else {
      menuBtn.classList.remove('close');
      menu.classList.remove('show');
      menuNav.classList.remove('show');
      navItems.forEach(item => item.classList.remove('show'));
      navItems.forEach(item => item.classList.add('close'));

      // Set Menu State
      showMenu = false;
  }
  }
    render() {                                                                // Constants for Nav bar
    return (
      <div className="navigation-menu">
        <div class="container">
            <div className="menu-btn" onClick= {this.toggleMenu}>
                <div class="btn-line"></div>
                <div class="btn-line"></div>
                <div class="btn-line"></div>
            </div>                        
            <div class="socialLinks">
                <a href="https://www.facebook.com/phillipe.shin" target="_blank"><ion-icon name="logo-facebook"></ion-icon></a>
                <a href="https://www.linkedin.com/in/phillipe-shin-61835a172/" target="_blank"><ion-icon name="logo-linkedin"></ion-icon></a>
                <a href="https://github.com/phillipeshin" target="_blank"><ion-icon name="logo-github"></ion-icon></a>
            </div>
        </div>                                                                    
          <nav className="menu">
            <ul class="menu-nav">
            <div class="portrait"></div>
                <li class="nav-item current">
                    <a href="#" class="nav-link">
                    Home
                    </a>
                </li>
                <li class="nav-item">
                    <a href="#" class="nav-link">
                    About Me
                    </a>
                </li>
                <li class="nav-item">
                    <a href="#" class="nav-link">
                    My Work
                    </a>
                </li>
                <div class="contactinfo">
                <div class="email">
                            <span><ion-icon name="mail"></ion-icon></span>
                            <h4>Email</h4>
                            <p>phillipeshin@gmail.com</p>
                </div>
                <div class="phone">
                            <span><ion-icon name="call"></ion-icon></span>
                            <h4>Phone</h4>
                            <p>253-320-8428</p>
                </div>
            </div>                
            </ul>
            </nav>

    </div>
        
    );
    }
    
}

export default Navigation;
